.an_container {
       bottom: 0;
       left: 0;
       opacity: 1;
       position: fixed;
       right: 0;
       top: 0;
       z-index: 999999999999;
       background-color: #fff;
}

.an_wrap {
       height: 100%;
       z-index: 2;
       position: relative;
}

.an_wrap_row {
       display: flex;
       flex-wrap: wrap;
       align-items: center;
       justify-content: center;
       height: 100%;
}

.an_media_,
.an_content_wrap {
       -ms-flex-preferred-size: 0;
       flex-basis: 0;
       -webkit-box-flex: 1;
       -ms-flex-positive: 1;
       flex-grow: 1;
       max-width: 100%;
}
.an_media_{
       display: flex;
       align-items: center;
       height: 100%;
       padding: 10px;
}

.an_media_ img {
       max-width: 84%;
   }

.an_content_wrap {
       display: flex;
       justify-content: center;
       align-items: center;
       max-width: 40%;
       height: 100%;
       background: rgba(0, 0, 0, 0.7);
}

.an_only_media .an_media_,
.an_only_text .an_content_wrap{
       max-width: 100%;
}

.an_content_in {
       padding: 5%;
}

.an_content {
       font-size: calc(15px + 0.3vh + 0.3vw);
       line-height: 1.6;
       text-align: center;
       display: -webkit-box;
       -webkit-box-orient: vertical;
       overflow: hidden;
       color: #fff;
}

.an_con_line-3{
       -webkit-line-clamp: 3;
}
.an_con_line-4{
       -webkit-line-clamp: 4;
}
.an_con_line-5{
       -webkit-line-clamp: 5;
}
.an_con_line-6{
       -webkit-line-clamp: 6;
}
.an_con_line-7{
       -webkit-line-clamp: 7;
}
.an_con_line-8{
       -webkit-line-clamp: 8;
}
.an_con_line-9{
       -webkit-line-clamp: 9;
}
.an_con_line-10{
       -webkit-line-clamp: 10;
}

.an_img_ {
       display: block;
       max-width: 100%;
       margin: 0 auto;
       height: auto;
}

.an_overlay_:before {
       background: rgba(0, 0, 0, 0.5);
       -webkit-backdrop-filter: blur(12px);
       backdrop-filter: blur(12px);
       position: absolute;
       content: "";
       width: 100%;
       height: 100%;
       left: 0;
       top: 0;
}
.an_overlay_{
       position: absolute;
       left: 0;
       bottom: 0;
       width: 100%;
       height: 100%;
       background-size: cover;
       background-position: center;
       background-repeat: no-repeat;
       z-index: 0;
}


//    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);


@media (orientation: portrait) {
       .an_wrap_row {
              flex-direction: column;
       }
       .an_media_,
       .an_content_wrap {
              height: calc(50vh - 2%);
       }
       .an_content_wrap {
              width: 100%;
              max-width: 100%;
       }


       .an_only_media .an_content_wrap,
       .an_only_text .an_media_{
              height: 100vh;
       }
}